// Well I guess your have some idea what you up to if you got this far. Any questions tweet @JBird608 or @JoeCurranCodes. //

// Font Types Imported //
// 1: CoreThin, 2: CoreThinItalic, 3:CoreRegular 4: CoreBold, 5: CoreBlack. //

// Colours - Standard //
$white: #fafafa;
$shady-white: #a2a2a2;
$purple: #62448c;
//$purple: #ff7a00; // Orange // ff8f00
$deep_purple: #4b2777;
$tinted_gray: #272a33;
$dark_gray: #2b2b2b;
$shadows: #121212;

// Colours - Fonts //
$faint_grey: #5e5e5e;
$light_blue: #41BEDD;

// Colours - Icons //
$dribbble: #ea4c89;
$twitter: #28aae1;
$pinterest: #ab171e;
$linkedin: #007bb6;
$behance: #005CFF;
$skype: #0078CA;
$email: #62448c;
//$email: #ff7a00; // Orange //

// Fonts //
$CoreThin: 'CoreThin', sans-serif;
$CoreThin-Italic: 'CoreThinItalic', sans-serif;
$CoreRegular: 'CoreRegular', sans-serif;
$CoreItalic: 'CoreItalic', sans-serif;
$CoreBold: 'CoreBold', sans-serif;
$CoreBlack: 'CoreBlack', sans-serif;

// Mixins //
@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

// @include transition(color .5s ease); //
@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity-ie: $opacity * 100;
  filter: alpha(opacity=$opacity-ie);
}

// @include opacity(0.8); //

// Core Basics //
body {
  font-family: $CoreRegular;
  background: #F5EFF4;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 0.5rem 0;
  font-family: $CoreBold;
}
h1 {
  font-size: 2rem;
}
h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

h4 {
  font-size: 1.2rem;
}

h5 {
  font-size: 1.2rem;
  border-bottom: 1px dashed lightgrey;
}

h6 {
  font-size: 1.2rem;
  background-color: #3b217a;
  margin-bottom: 0;
  padding: 0.6rem 1rem 0.2rem;
  color: white;
  font-family: $CoreRegular;
  border-radius: 5px 5px 0 0;
}
a {
  text-decoration: none;
  border-bottom: 1px dashed $purple;
  color: $purple;
  @include transition(all .5s ease);
  font-family: $CoreBold;

  &:hover {
    color: #ffcc03;
    border-bottom: none;
  }

  &:visited, &:active {

  }
}
input[type="text"], input[type="password"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="month"], input[type="week"], input[type="email"], input[type="number"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], input[type="color"], textarea {
  font-family: $CoreThin;
  font-size: 1.2rem;
  padding: 1.5rem 1rem;
}
input[type="textarea"] {
  resize: none;
}
textarea {
  resize: none;
}
input[type="submit"] {
  padding: 1rem;
  font-family: $CoreBold;
  font-size: 1rem;
  color: white;
  background-color: $purple;
  border: none;
  cursor: pointer;
  @include transition(background-color .5s ease);

  &:hover {
    background-color: $deep_purple;
  }
}

// Core Eagle #IDs //
