}

#core_navigation {
  padding: 1rem 0 1rem 0;
  color: $white;

  //background: -webkit-linear-gradient(135deg, #673AB7 10%, #3b217a 60%); /* Chrome 10+, Saf5.1+ */
  //background: -moz-linear-gradient(135deg, #673AB7 10%, #3b217a 60%); /* FF3.6+ */
  //background: -ms-linear-gradient(135deg, #673AB7 10%, #3b217a 60%); /* IE10 */
  //background: -o-linear-gradient(135deg, #673AB7 10%, #3b217a 60%); /* Opera 11.10+ */
  //background: linear-gradient(135deg, #673AB7 10%, #3b217a 60%); /* W3C */
  background-image: url('../../img/bg.png');
  background-position: right;
  background-size: auto;
  background-repeat: no-repeat;
  border-bottom: 1px solid lightgray; // TODO: CSS - Change to a Var Value! //

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;

    li {
      margin: 0 0.5rem;
      display: inline-block;
      text-align: center;
      align-items: center;

      &:first-child {
        margin-left: 0;
        -webkit-align-self: flex-start;
        font-size: 1.5rem;
        margin-right: auto;

        @media screen and (max-width: 610px) {
          width: 100%;
        }

        a {
          font-family: $CoreBold;

          @media screen and (max-width: 610px) {
            &:first-child {
              display: block;
              font-size: 2rem;
              margin-bottom: 1rem;

              &:hover {
                color: #ffcc03;
                border: none;
              }
            }
            &:nth-child(2) {
              display: none;
            }
          }
          &:hover {
            color: $white;
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }
      @media screen and (max-width: 610px) {
        flex-grow: 1;
      }
    }
  }
  a {
    font-family: $CoreThin;
    color: $white;
    border-bottom: none;

    &:hover {
      color: #ffcc03;
      border-bottom: double;
    }
  }
}

#core_content {
  margin: 1rem auto;

  // BLOG PAGES //
  #core_blog_cover {
    margin: 2rem auto;

    h1 {
      text-align: center;
      color: #333333;
      text-transform: uppercase;
    }
    p {
      width: 70%;
      margin: 0 auto 1rem;
      font-size: 1.2rem;
      text-align: center;
    }
  }
  #core_blog {

    a {
      color: black;
      font-family: "CoreRegular", sans-serif;

      &:hover {
        color: black;
      }
    }
    article {
      padding: 0 0 0 0;
      margin: 0 0 2rem 0;

      .posted {
        margin: 0.3rem 0;
        padding: 0.3rem 0;
        color: darkgray;
        //border-top: 1px solid darkgray;
        //border-bottom: 1px solid darkgray;
        font-size: 0.8rem;
      }
      h2 {
        color: #673AB7;

        &:hover {
          color: #ffcc03;
        }
      }
      img {
        width: 100%;
        height: 300px;
        margin-bottom: 1rem;
      }
    }
  }
  .article_head {
    margin: 0 0 1.5rem 0;

    h1 {
      padding: 0.5rem 2rem 0 0;
      color: white;
      line-height: 2.6rem;

      i {
        font-size: 1.2rem;
        vertical-align: middle;
      }
    }
    #cover {
      position: relative;
      top: 0;
      left: 0;
      z-index: -1;
    }
    #cover img {
      position: relative;
      margin: auto;
      max-height: 300px;
      min-width: 100%;
    }
    .article_title {
      background-color: #3b217a;
      width: auto;
      margin-top: -5rem;

      &:after {
        content: " ";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: #3b217a;
        z-index: -1;
        -webkit-transform: skew(20deg);
        -moz-transform: skew(20deg);
        -o-transform: skew(20deg);
      }
    }
  }
  article {
    border-bottom: 3px double #673AB7;
    padding: 1.5rem 1rem 2.5rem 1rem;
    font-size: 1.2rem;

    .inline-list > li {
      margin: 0 1rem 0 0;
    }
    .inline-list > li > i, .inline-list > li > a > i{
      display: inline;
      margin-right: 0.5rem;
    }
    .inline-list {
      border-bottom: 1px dashed lightgray;
      padding: 0 0 0.6rem 0;

      a {
        border-bottom: none;
      }
    }
    p {
      font-size: 0.9rem;
      margin: 0.5rem 0;
    }
    em {
      font-family: $CoreItalic;
    }
    ul {
      font-size: 0.9rem;
      list-style: square;

      li {

      }
    }
    code {
      font-family: monospace;
      font-size: 1rem;
      padding: 0.2rem 0.5rem;
      border: 1px solid #3b217a;
      border-radius: 5px;
      background-color: #211f21;
      color: white;
    }
    pre > code {
      border: none;
      background-color: transparent;
      margin-top: 0 !important;
    }
    pre[class*="language-"] {
      margin-top: 0 !important;
      border-radius: 0 0 0.3rem 0.3rem !important;
    }
    blockquote {
      background-color: rgba(65, 190, 221, 0.1);
      border-left: 0.2rem solid $light_blue;
      padding: 1rem;

      &:before {
        content: '\201C';
        float: left;
        font-size: 7rem;
        line-height: 1rem;
        margin: 3.5rem 0.25rem 0 0;
        color: rgba(65, 190, 221, 0.3);
      }
      a {
        border-bottom: none;

        &:before {
          content: '\2014\0020';
        }
      }
    }
    img {
      margin: 0;
      display: block;
    }
    .right {
      text-align: right;
    }
  }

  // CONTACT PAGE //
  #core_contact {
    margin: 2rem 0 0;
    padding: 0;

    h2 {
      color: white;
    }
    #core_contact_head {
      h1 {
        color: #333333;
        text-transform: capitalize;
      }
      p {
        margin: 0 auto 1rem;
      }

    }
    .form {
      padding: 2rem 1rem 1rem;

      label {
        color: gray;
      }
    }
  }
}
#core_footer_before {
  padding: 2rem 0;
  background-color: $deep_purple;
  color: white;
  font-family: $CoreThin;

  h3 {
    color: white;
  }
  a {
    color: white;

    &:hover {
      color: #ffcc03;
      border-bottom: none;
    }
  }
}
#core_footer {
  margin: 1rem 0;
  color: $white;

  h4 {
    margin: 1rem 0 0;
  }

  p {
    margin: 0;
  }

  form {
    ul {
      list-style: none;
      padding: 0;
    }
    input[type="radio"] {
      display: none;
    }
    input[type="radio"]:checked + label {
      background-color: rgba(43, 43, 43, 1.0);
      content: "\f00c";
    }
    input[type="radio"]:checked + label:before {
      font-family: 'FontAwesome';
      content: "\f00c\0020";
    }
    label {
      color: $white;
      padding: 0.5rem;
      margin: 0.3rem 0;
      border-radius: 0.2rem;
      background-color: rgba(43, 43, 43, 0.5);
      width: 100%;
      display: block;
      font-size: 0.9rem;

      &:before {
        font-family: 'FontAwesome';
        content: "\f069";
        margin-right: 0.5rem;
      }

      &:hover {
        background-color: rgba(43, 43, 43, 0.8);
      }

    }
    input[type="button"] {
      font-size: 0.9rem;
      padding: 1rem;
      margin: 0.3rem;
      border-radius: 0.2rem;
      border: none;
      color: $white;
      background-color: rgba(43, 43, 43, 0.5);
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }

      &:after {
        font-family: 'FontAwesome';
        content: "\f061";
      }

      &:hover {
        background-color: rgba(43, 43, 43, 0.8);
      }
    }
    .align-right {
      text-align: right;
      vertical-align: middle;

      .voteas {
        color: $shady-white;
        margin: 0 1rem;
      }
    }
    .btn-second {
      background-color: rgba(43, 43, 43, 0.3) !important;

      &:hover {
        background-color: rgba(43, 43, 43, 0.8) !important;
      }
    }
  }
}

#core-comments {
  margin: 0rem 0 1rem 0;

  .row {
    border-top: 6px solid $purple;

  }

  h4 {
    margin: 0.5rem 0 0 0;

    &:after {
      content: '?';
    }
  }

  p {
    margin: 0 0 0.5rem 0;
  }

  .bold {
