  }

  .core-comment-first {
    padding: 0.5rem;
    border: 1px solid $faint_grey;
    margin-bottom: 0.5rem;

    .comment-user-display {
      max-height: 5rem;
      max-width: 5rem;

      // TODO Remove this for production //
      height: 5rem;
      width: 5rem;
    }

    .comment {

    }

    .core-comment-second {
      padding: 0.5rem 0.5rem 0.5rem 6rem;
      border: 1px solid $faint_grey;
      margin-bottom: 0.5rem;
    }

