#core_end {
  font-family: $CoreRegular;
  background-color: $shadows;
  width: 100%;
  text-align: center;
  color: $shady-white;
  padding: 0.5rem 0;

  p {
    font-size: 0.7rem;
    margin: 0;
  }

  a {
    text-transform: capitalize;
    border-bottom: 1px dashed $shady-white;
    color: inherit;
    @include transition(color .5s ease);

    &:hover {
      border-bottom: none;
      color: #ffcc03;
    }
  }

}

// Core Eagle .classes //

.bg-highlight {
  background-color: $purple;
  padding: 1rem 2rem;
}
.border-bottom {
  border-bottom: 3px double #333333;
}
.post-poll {

  &:after {
    content: "?";
  }
}

// Type //
b, strong, .bold {
  font-family: 'CoreBold';
}
.small {
  font-size: 0.8rem;
}
.italics {
  font-style: italic;
  font-family: $CoreThin-Italic;
}

.no-margin {
  margin: 0;
}

.date {
  position: absolute;
  left: 1.9375rem;
  width: 4rem;
  text-align: center;

  .month {
    background-color: #3b217a;
    color: white;
    font-size: 0.6rem;
    padding: 0.2rem;
  }
  .day {
    background-color: #673AB7;
    color: white;
    font-size: 1rem;
    padding: 0.6rem;
  }
}

#pagination {
  display: flex;
}
ul.pagination {
  display: inline-block;
  margin: auto;

  li {
    min-width: 28px;
    background-color: lightgray;
    border-radius: 3px;
    text-align: center;

    a {
      border: none;
      background-color: lightgray;
      color: white;

      &:hover {
        background-color: $purple;
        color: white !important;
      }
    }
  }
  .active {
    background-color: #ffcc03;
    padding: 1px;
  }
  .disabled {
    background-color: transparent;
  }
}

// Core Eagle Font Awesome //
.fa-margin-right {
  margin-right: 0.5rem;
